* {
    padding: 0;
    margin: 0;
}

body {
    background-color: #f7f7f7;
    color: #3f3f3f;
    font-family: monospace;
    padding: 5vw 8vw;
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.5em;
}

h3, legend {
    font-size: 1.2em;
    font-weight: bold;
}

p, li, a {
    font-size: 1.03em;
    margin: 0.5em;
    color: #3f3f3f;
    font-family: monospace;
    text-decoration: none;
}

hr {
    display: block;
    height: 0.2em;
    border: 0;
    border-top: 0.2em solid #ff6c00;
    margin: 0.4em 0;
}

ul {
  list-style: none;
}

ul li::before {
  content: "\2022";
  color: #ff6c00;
  font-weight: bold;
  display: inline-block; 
  width: 1em;
}

span {
    color: #59678e;
}

h3::after {
  width: 100%;
  height: 1em;
  background-color: #ff6c00;
  display: inline-block;
}

fieldset {
    border: 0;
    border-top:0.2em solid #ff6c00;
}

.progres fieldset {
    border: 0;
    border-top:0.3em solid #ff6c00;
}

.fab {
    color: #ff6c00;
    font-size: 2em;
    width: 1.3em;
    text-align: center;
    margin: 0.1em 0;
}

.social {
    display: flex;
    align-items: center;
    white-space: nowrap;
}

.container {
    display: flex;
}

.column {
    display: flex;
    flex-direction: column;
    margin: 0 1em;
}

.right {
    display: flex;
    flex-direction: column;
}

@media only screen and (max-width: 768px){
    .container {
        flex-direction: column-reverse;
    }
}

.progress {
    display: flex;
    align-items: center;
}
